@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&family=Ubuntu:wght@300&display=swap');

:root{
  /* primary */
  --Bright-Blue: hsl(220, 98%, 61%);
--Check-Background: linear-gradient(hsl(192, 100%, 67%) ,hsl(280, 87%, 65%));
  /* light theme */
--Very-Light-Gray: hsl(0, 0%, 98%);
--Very-Light-Grayish-Blue: hsl(236, 33%, 92%);
--Light-Grayish-Blue: hsl(233, 11%, 84%);
--Dark-Grayish-Blue: hsl(236, 9%, 61%);
--Very-Dark-Grayish-Blue: hsl(235, 19%, 35%);
/* dark theme */
--Very-Dark-Blue: hsl(235, 21%, 11%);
--Very-Dark-Desaturated-Blue: hsl(235, 24%, 19%);
--Light-Grayish-Blue: hsl(234, 39%, 85%);
--Light-Grayish-Blue: hsl(236, 33%, 92%);
--Dark-Grayish-Blue: hsl(234, 11%, 52%);
--Very-Dark-Grayish-Blue: hsl(233, 14%, 35%);
--Very-Dark-Grayish-Blue: hsl(237, 14%, 26%);
/* heights */
--list-height: 45px;
}

body {
  margin: 0;
  font-family: 'Josefin Sans','Ubuntu', sans-serif;
  background-color: var(--Very-Dark-Blue);
  background-image: url(../public/images/bg-desktop-dark.jpg);
  background-repeat: no-repeat;
  text-transform: capitalize;
}

.section-center{
  max-width: 500px;
  height: auto;
  margin: 100px auto;
}

header{
  width: 100%;
  display: flex;
  justify-content: space-between;
}

header h1{
  font-size: 30px;
  margin-top: 0;
  text-transform: uppercase;
  letter-spacing: 10px;
  color: var(--Very-Light-Gray);
}

header img{
  width: 20px;
  height: 20px;
}

.todo-form{
  width: 100%;
  height: var(--list-height);
  background-color: var(--Very-Dark-Desaturated-Blue);
  display: flex;
  justify-content: space-between;
}

.list-container{
  width: 100%;
  list-style: none;
  padding: 0;
  margin-bottom: 0;
  /* overflow-y: scroll;
  max-height: 450px; */
}

::-webkit-scrollbar{
  width: 2px;
}

::-webkit-scrollbar-thumb{
  background-color: var(--Very-Light-Grayish-Blue);
}

input{
  width: 90%;
  border: none;
  outline: none;
  background-color: var(--Very-Dark-Desaturated-Blue);
  color: var(--Very-Light-Gray);
  font-family: 'Josefin Sans','Ubuntu', sans-serif;
}


.list-item{
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid var(--Very-Dark-Grayish-Blue);
  height: var(--list-height);
  background-color: var(--Very-Dark-Desaturated-Blue);
  position: relative;
}

.list-toggle{
  height: 35px;
  font-size: 15px;
  color: var(--Dark-Grayish-Blue);
  justify-content: space-evenly;
}

.item{
  width: 90%;
  color: var(--Very-Light-Gray);
}

.list-checked{
  text-decoration: line-through;
  color: var(--Dark-Grayish-Blue);
}

.btn{
  height: 20px;
  cursor: pointer;
  width: 20px;
  border-radius: 50%;
  margin: auto;
  border: 1px solid var(--Very-Dark-Grayish-Blue);
  background-color: var(--Very-Dark-Desaturated-Blue);
}

.checked{
  background: url(../public/images/icon-check.svg);
  background-color: var(--Bright-Blue);
  background-repeat: no-repeat;
  background-position: center;
}

.remove-btn{
  border: none;
  background: none;
  color: var(--Dark-Grayish-Blue);
  padding: 0;
  margin-inline: 10px;
  font-size: 13px;
  font-family: 'Josefin Sans','Ubuntu', sans-serif;
}

.remove-btn:hover{
  color: var(--Very-Light-Grayish-Blue);
}

.active{
  color: var(--Bright-Blue);
}

#cross{
  position: absolute;
  right: 10px;
}

@media (max-width:375px) {
  body{
    background-image: url(../public//images/bg-mobile-dark.jpg);
  }
  .section-center{
    max-width: 320px;
    margin: 50px auto;
  }
  .remove-btn{
    margin-inline: 3px;
  }
  .list-toggle{
    font-size: 12px;
  }
}